import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {  Modal, Form, Input, Icon, Button, Select } from 'antd';

const FormItem = Form.Item;
const createForm = Form.create;
const Option = Select.Option;


export class TermForm extends Component {

  static propTypes = {
    formValues: PropTypes.object,
    formType: PropTypes.string,
    form: PropTypes.object,
    handleHide: PropTypes.func,
    handleOK: PropTypes.func
  }

  /**
   * 弹层点击取消操作
   */
  handleFormCancel = () => {
    this.props.handleHide();
  }

  /**
   * 弹层点击确定操作
   */
  handleFormOk = () => {
    const fields = this.props.form.getFieldsValue();
    if (this.props.formValues && this.props.formValues._id) {
      fields._id = this.props.formValues._id;
    }
    this.props.handleOK(fields);
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const title = this.props.formType === 'add' ? '添加分类' : '编辑分类';
    const visible = true;
    const termTypes = ['post', 'page'];
    return (
      <div>
        <Modal title={title}
               visible={visible}
               onOk={this.handleFormOk}
               onCancel={this.handleFormCancel}>

          <Form layout="horizontal" onSubmit={this.handleSubmit}>

            <FormItem
              label="分类名称"
              hasFeedback
            >
              {getFieldDecorator('term_name', {
                rules: [{ required: true, message: '分类名称不能为空!' }],
              })(
                <Input />
              )}
            </FormItem>

            <FormItem
              label="缩略名(英文字母)"
              hasFeedback
            >
              {getFieldDecorator('term_slug', {
                rules: [{
                  required: true, message: '缩略名不能为空!!',
                }],
              })(
                <Input />
              )}
            </FormItem>

            <FormItem
              label="分类类型"
              hasFeedback
            >
              {getFieldDecorator('term_type', {
                rules: [
                  { required: true, message: '分类类型不能为空!' },
                ],
              })(
                <Select>
                  {
                    termTypes.map((val) => {
                      return <Option value={val} key={val}>{val}</Option>
                    })
                  }
                </Select>
              )}
            </FormItem>

            <FormItem
              label="分类描述"
              hasFeedback
            >
              {getFieldDecorator('term_desc', {
                rules: [{
                  required: true, message: '描述不能为空!',
                }],
              })(
                <Input type="textarea"  />
              )}
            </FormItem>

          </Form>

        </Modal>
      </div>
    )
  }

}

export default createForm({
  mapPropsToFields(props) {
    if (props.formType === 'add') {
      return {}
    }
    const {term_name, term_type, term_slug, term_desc} = props.formValues;
    return {
      term_name: {
        ...term_name,
        value: term_name,
      },
      term_type: {
        ...term_type,
        value: term_type,
      },
      term_slug: {
        ...term_slug,
        value: term_slug
      },
      term_desc: {
        ...term_desc,
        value: term_desc
      }
    };
  }
})(TermForm);



