import callApi from '../../utils/callApi';

/**
 * 获取分类列表
 * @returns {function()}
 */
const getList = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/term/list';
    return callApi.get(requestUrl, formdata).then(res => {
      return Promise.resolve(dispatch({
        type: 'TERM_LIST',
        termlist: res.data
      }))
    });
  }
}

/**
 * 添加分类
 * @param formdata
 * @returns {function()}
 */
const postAddTerm = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/term/add';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'TREM_ADD',
        info: res.data
      }));
    })
  }
}

/**
 * 编辑分类
 * @param formdata
 * @returns {function()}
 */
const postEditTerm = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/term/update';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'TERM_EDIT',
        index: formdata.index,
        info: res.data
      }));
    });
  }
}

/**
 * 删除分类信息
 * @param formdata
 * @returns {function()}
 */
const postDeleteTerm = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/term/delete';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'TERM_DELETE',
        index: formdata.index
      }));
    });
  }
}

export default {
  getList,
  postAddTerm,
  postEditTerm,
  postDeleteTerm
}



