import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import { Table, Menu, Popconfirm, message, Button, Icon } from 'antd';


import termActions from '../action'
import appActions from '../../app/action'
import TermForm from './TermForm';

export class Term extends Component {

  static propTypes = {
    term: PropTypes.object,
    appActions: PropTypes.object,
    termActions: PropTypes.object
  }

  constructor (props) {
    super(props);

    /**
     * table头部
     * @type {*[]}
     */
    this.columns = [{
      title: '分类名称',
      width: 150,
      dataIndex: 'term_name',
      key: 'name',
    }, {
      title: '缩略名',
      width: 150,
      dataIndex: 'term_slug',
      key: 'slug',
    }, {
      title: '栏目类型',
      width: 150,
      dataIndex: 'term_type',
      key: 'type',
    }, {
      title: '分类描述',
      dataIndex: 'term_desc',
      key: 'desc',
    }, {
      title: '操作',
      render: (text, record, index) => {

        const self = this;
        function handleTermDelete() {
          self.handleTermDelete(record, index)
        }
        function handleTermEdit() {
          self.handleTermEdit(record, index)
        }

        return (
          <span>
            <a href="#" onClick={handleTermEdit}>编辑</a>
            <span className="ant-divider" />
            <Popconfirm title="确定删除此分类么?" onConfirm={handleTermDelete} >
              <a href="#">删除</a>
            </Popconfirm>
          </span>
        )
      },
    }];
  }

  state = {
    index: null,
    formType: 'add',
    formValues: null,
    formVisible: false
  }

  componentDidMount () {
    this.getTermList();
  }

  /**
   * 获取分类列表
   */
  getTermList = () => {
    const { appActions, termActions } =  this.props;
    appActions.loading(true).then(() => {
      return termActions.getList()
    }).then(() => {
      appActions.loading(false);
    }).catch(err => {
      message.error(err.msg);
    })
  }

  /**
   * 添加分类
   * @param e
   */
  handleTermAdd = (e) => {
    this.setState({
      formType: 'add',
      formValues: null,
      formVisible: true
    })
  }

  /**
   * 隐藏modal弹出层
   */
  handleHideModal = () => {
    this.setState({
      formValues: null,
      formVisible: false
    })
  }

  /**
   * 提交表单
   * @param formdata
   */
  handleTermSubmit = (formdata) => {
    const {termActions} = this.props;
    if (formdata._id) {
      formdata.index = this.state.index;
      termActions.postEditTerm(formdata).then(data => {
        this.handleHideModal();
      }).catch(err => {
        if (typeof err.msg === 'object') {
          message.error(err.msg.message)
        } else {
          message.error(err.msg)
        }
      });
    } else {
      termActions.postAddTerm(formdata).then(data => {
        this.handleHideModal();
      }).catch(err => {
        if (typeof err.msg === 'object') {
          message.error(err.msg.message)
        } else {
          message.error(err.msg)
        }
      });
    }
  }

  handleTermEdit = (formValues, index) => {
    this.setState({
      index,
      formType: 'edit',
      formVisible: true,
      formValues
    })
  }
  

  /**
   * 操作删除分类
   * @param _id
   */
  handleTermDelete = (formdata, index) => {
    this.props.termActions.postDeleteTerm({
      index,
      _id: formdata._id,
    }).then(data => {
      message.success('删除成功');
    }).catch(err => {
      message.error(err.msg);
    })
  }

  render() {

    const {term} = this.props;
    const list = term.get('termlist') && term.get('termlist').toArray();

    return (
      <div>

        <Button style={{float: 'right', top: '10px'}} type="primary" onClick={this.handleTermAdd}><Icon type="folder-add" />添加分类</Button>
        <Menu selectedKeys={['list']}
              mode="horizontal">
          <Menu.Item key="list">
            分类列表
          </Menu.Item>
        </Menu>

        <Table
          bordered
          rowKey="_id"
          columns={this.columns}
          dataSource={list}
          className="mt20"
          pagination={false}
        />
        {
          this.state.formVisible && <TermForm
            formType={this.state.formType}
            formValues={this.state.formValues}
            handleHide={this.handleHideModal}
            handleOK={this.handleTermSubmit}
          />
        }
      </div>
    )
  }

}

export default connect((state) => {
  return  {
    term: state.get('term')
  }
}, dispatch => {
  return {
    termActions: bindActionCreators(termActions, dispatch),
    appActions: bindActionCreators(appActions, dispatch)
  }
})(Term);



